import { useState, useEffect } from 'react'
import { BigNumber, constants, utils } from 'ethers'
import { Link } from 'react-router-dom'
import Image from 'react-graceful-image'
import { useWeb3 } from '../web3'
import { useData } from '../dataContext'
import { useAuctionCatInfo } from '../dataContext/cats'
import { useCatOwner } from '../dataContext/cats'

const Container = ({ children }) => {
  return (
    <div className="transition duration-500 ease-in-out transform hover:scale-110">
      {children}
    </div>
  )
}

const LoadingError = ({ children, catId }) => {
  return (
    <div className="h-full text-center text-sm p-4">
      <div>DoomCat #{catId.toString()}</div>
      <div className="flex flex-col justify-center mt-6">{children}</div>
    </div>
  )
}

const DoomCatAuctionCard = ({ tokenId, catLoadedCallback, over }) => {
  const [tokenURI, setTokenURI] = useState()
  const [catId] = useState(BigNumber.from(tokenId))
  const { account } = useWeb3()

  useEffect(() => {
    setTokenURI(`https://api.doom.cat/metadata/${tokenId}`)
  }, [tokenId])

  const { doomCatContract } = useData()
  const [catInfo, error] = useAuctionCatInfo(doomCatContract, catId, tokenURI, account)

  const owner = useCatOwner(doomCatContract, catId)

  useEffect(() => {
    if (catInfo && catLoadedCallback) {
      catLoadedCallback(tokenId)
    }
  }, [catInfo, catLoadedCallback, tokenId])

  const [myTopBid, setMyTopBid] = useState(false)
  useEffect(() => {
    if (!catInfo || !account) return
    setMyTopBid(catInfo.highBidOwner.toLowerCase() === account.toLowerCase() && !over)
  }, [account, catInfo, over])

  const [myWinningBid, setMyWinningBid] = useState(false)
  useEffect(() => {
    if (!catInfo || !account) return
    setMyWinningBid(catInfo.highBidOwner.toLowerCase() === account.toLowerCase() && over)
  }, [account, catInfo, myTopBid, over])

  const [myLowBid, setMyLowBid] = useState(false)
  useEffect(() => {
    if (!catInfo) return
    setMyLowBid(catInfo.bidByAddress.gt(0) && !myTopBid && !myWinningBid)
  }, [catInfo, myTopBid, myWinningBid])

  if (error) {
    return (
      <Container>
        <LoadingError catId={tokenId}>
          <div>Error: {error.message}</div>
        </LoadingError>
      </Container>
    )
  } else if (catInfo) {
    return (
      <Link to={{
        pathname: `/auctions/${tokenId}`,
        state: { catInfo }
      }}>
        <Container>
          <div className="text-center text-sm">
            <div className="mb-2">{catInfo.name}</div>
            {catInfo.image && <Image
              src={catInfo.image}
              retry={{ count: 30, delay: 3 }}
              width="100%"
              alt={catInfo.name}
            />}
            <div className="mt-1">Top Bid</div>
            <div>{parseFloat(utils.formatEther(catInfo.highBidAmount)).toFixed(2)} ETH</div>
            <div className="text-3xl">
              {myTopBid && <div>{""}</div>}
              {myLowBid && <div>{""}</div>}
              {myWinningBid && owner === constants.AddressZero && <div>{""}</div>}
            </div>
          </div>
        </Container>
      </Link>
    )
  } else {
    return (
      <Container>
        <LoadingError catId={tokenId}>
          <div>Loading...</div>
        </LoadingError>
      </Container>
    )
  }
}

export default DoomCatAuctionCard
