import { useState, useEffect } from 'react'
import { BigNumber, constants, utils } from 'ethers'
import FetchRetry from 'fetch-retry'
const fetchRetry = FetchRetry(fetch)

function useCatBalance(doomCatRescue, address) {
  const [catBalance, setCatBalance] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue || !address || !utils.isAddress(address)) {
      setCatBalance(BigNumber.from(0))
      return
    }

    function getCatBalance() {
      doomCatRescue.balanceOf(address)
        .then(setCatBalance)
        .catch(console.error)
    }

    getCatBalance()

    function increaseCatBalance() {
      getCatBalance()
    }

    function decreaseCatBalance() {
      getCatBalance()
    }

    const transferCatInFilter = doomCatRescue.filters.Transfer(null, address, null)
    const transferCatOutFilter = doomCatRescue.filters.Transfer(address, null, null)

    doomCatRescue.on(transferCatInFilter, increaseCatBalance)
    doomCatRescue.on(transferCatOutFilter, decreaseCatBalance)

    return () => {
      doomCatRescue.removeListener(transferCatInFilter, increaseCatBalance)
      doomCatRescue.removeListener(transferCatOutFilter, decreaseCatBalance)
    }
  }, [doomCatRescue, address])

  return catBalance
}

function useTokensOfOwnerByIndexes(doomCatRescue, address, balance) {
  const [catIds, setCatIds] = useState([])

  useEffect(() => {
    if (!doomCatRescue || !address || balance.eq(BigNumber.from(0)) || !utils.isAddress(address)) {
      setCatIds([])
      return
    }

    Promise.all([...new Array(balance.toNumber()).keys()].map(i => doomCatRescue.tokenOfOwnerByIndex(address, i)))
      .then(tokenIds => tokenIds.sort((a, b) => a.sub(b)))
      .then(setCatIds)
      .catch(console.error)
  }, [address, balance, doomCatRescue])

  return catIds
}

function useTokenURI(doomCatRescue, tokenId) {
  const [tokenURI, setTokenURI] = useState()

  useEffect(() => {
    if (!doomCatRescue || !tokenId || tokenId.eq(0)) return

    doomCatRescue.tokenURI(tokenId.toString())
      .then(setTokenURI)
      .catch(console.error)
  }, [doomCatRescue, tokenId])

  return tokenURI
}

function useCatInfo(doomCatRescue, tokenId, tokenURI, existingCatInfo) {
  const [error, setError] = useState(null)
  const [catInfo, setCatInfo] = useState(null)

  useEffect(() => {
    if (existingCatInfo) {
      setCatInfo(existingCatInfo)
    }
  }, [existingCatInfo])

  useEffect(() => {
    if (!doomCatRescue || !tokenId || tokenId.eq(0) || existingCatInfo || !tokenURI) return

    fetchRetry(tokenURI, {
      retries: 20,
      retryDelay: 3000,
      retryOn: [401]
    }).then(response => {
      if (response.ok) {
        return response.json()
          .then(
            result => {
              return doomCatRescue.catNames(tokenId).then(_name => {
                const name = _name ? _name : result.name
                return setCatInfo({ ...result, name, isNamed: !!_name, id: tokenId.toNumber() })
              }).catch(error => {
                console.error(error)
                setError(new Error("Unable to load cat"))
              })
            },
            error => {
              console.error(error)
              setError(new Error("Unable to load cat"))
            }
          )
      } else {
        setError(new Error("Unable to load cat"))
      }
    })
      .catch(error => {
        console.error(error)
        setError(new Error("Unable to load cat"))
      })

    function updateName(_, catId, name) {
      if (!catId.eq(tokenId)) return
      setCatInfo(catInfo => ({ ...catInfo, name, isNamed: true }))
    }

    doomCatRescue.on("NameCat", updateName)

    return () => {
      doomCatRescue.removeListener("NameCat", updateName)
    }
  }, [doomCatRescue, tokenId, existingCatInfo, tokenURI])

  return [catInfo, error]
}

function useAuctionCatInfo(doomCatRescue, tokenId, tokenURI, account, existingCatInfo) {
  const [classicalCatInfo, classicalError] = useCatInfo(doomCatRescue, tokenId, tokenURI, existingCatInfo)

  const [error, setError] = useState(null)
  const [catInfo, setCatInfo] = useState({
    bidCount: BigNumber.from(0),
    highBidAmount: BigNumber.from(0),
    highBidOwner: constants.AddressZero,
    bidByAddress: BigNumber.from(0),
  })

  useEffect(() => {
    if (classicalCatInfo) {
      setCatInfo(catInfo => ({ ...catInfo, ...classicalCatInfo }))
    }
  }, [classicalCatInfo])

  useEffect(() => {
    if (classicalError) {
      setError(classicalError)
    }
  }, [classicalError])

  useEffect(() => {
    if (!doomCatRescue || !tokenId || tokenId.eq(0) || !tokenURI) return

    Promise.all([
      doomCatRescue.bidCount(tokenId),
      doomCatRescue.highBidAmount(tokenId),
      doomCatRescue.highBidOwner(tokenId),
      account ? doomCatRescue.bidsByTokenByAddress(tokenId, account) : Promise.resolve(BigNumber.from(0))
    ])
      .then(([
        bidCount,
        highBidAmount,
        highBidOwner,
        bidByAddress,
      ]) => {
        setCatInfo(catInfo => ({ ...catInfo, bidCount, highBidAmount, highBidOwner, bidByAddress }))
      })
      .catch(error => {
        console.error(error)
        setError(new Error("Unable to load cat"))
      })

    const updateCatAuction = (highBidOwner, catId, highBidAmount, bidCount) => {
      if (!catId.eq(tokenId)) return

      setCatInfo(catInfo => ({ ...catInfo, highBidOwner, highBidAmount, bidCount }))
      
      if (highBidOwner.toLowerCase() === account) {
        setCatInfo(catInfo => ({ ...catInfo, bidByAddress: highBidAmount }))
      }
    }

    doomCatRescue.on("Bid", updateCatAuction)

    const updateWithdrawLowBid = (lowBidOwner, catId, amount) => {
      if (!catId.eq(tokenId)) return
      if (lowBidOwner.toLowerCase() !== account.toLowerCase()) return

      setCatInfo(catInfo => ({ ...catInfo, bidByAddress: BigNumber.from(0) }))
    }

    doomCatRescue.on("WithdrawLowBid", updateWithdrawLowBid)

    return () => {
      doomCatRescue.removeListener("Bid", updateCatAuction)
      doomCatRescue.removeListener("WithdrawLowBid", updateWithdrawLowBid)
    }
  }, [account, doomCatRescue, tokenId, tokenURI])

  return [catInfo, error]
}

function useCatOwner(doomCatRescue, tokenId) {
  const [owner, setOwner] = useState(constants.AddressZero)

  useEffect(() => {
    if (!doomCatRescue || tokenId.eq(0)) return

    doomCatRescue.ownerOf(tokenId)
      .then(setOwner)
      .catch(console.error)

    const updateOwner = (_, newOwner, catId) => {
      if (!catId.eq(tokenId)) return
      setOwner(newOwner)
    }

    doomCatRescue.on("Transfer", updateOwner)

    return () => {
      doomCatRescue.removeListener("Transfer", updateOwner)
    }
  }, [doomCatRescue, tokenId])

  return owner
}

export {
  useCatBalance,
  useTokensOfOwnerByIndexes,
  useTokenURI,
  useCatInfo,
  useAuctionCatInfo,
  useCatOwner,
}