import { useState, useEffect } from 'react'

function useMyEns(account, provider, block, chainId) {
  const [myEns, setMyEns] = useState(null)

  useEffect(() => {
    if (!account || !provider || chainId !== 1) {
      setMyEns(null)
      return
    }

    provider.lookupAddress(account)
      .then(setMyEns)
      .catch(console.error)
  }, [account, block, provider, chainId])

  return myEns
}

export {
  useMyEns,
}
