import { useState, useEffect, useRef, useCallback } from 'react'
import { BigNumber } from 'ethers'
import Jumbo from "../components/Jumbo"
import DoomCatAuctionCard from '../components/DoomCatAuctionCard'
import { useData } from '../dataContext'
import { useTimeSplitter } from '../utils/time'

const NextAuctionIn = () => {
  const { nextAuctionIn } = useData()
  const { d, h, m, s } = useTimeSplitter(nextAuctionIn)

  return (
    <Jumbo>
      <div className="text-center">
        <div className="uppercase text-lg mb-2">next genesis auction starts in</div>
        <div>{d} days {h} hours {m} minutes {s} seconds</div>
      </div>
    </Jumbo>
  )
}

const AuctionsAreOver = () => {
  return (
    <Jumbo>
      <div className="text-center">
        <div className="uppercase text-xl mb-2">genesis doomcat auctions are over!</div>
        <div className="uppercase">their souls have been saved</div>
      </div>
    </Jumbo>
  )
}

const CurrentAuction = () => {
  const { inAuctionNumber, catsPerAuction, auctionEndsIn } = useData()

  const { d, h, m, s } = useTimeSplitter(auctionEndsIn)

  const [catIds, setCatIds] = useState([])
  useEffect(() => {
    setCatIds(
      [...new Array(catsPerAuction.toNumber()).keys()]
        .map(i => {
          return i + 1 + inAuctionNumber.sub(1).mul(catsPerAuction).toNumber()
        })
    )
  }, [catsPerAuction, inAuctionNumber])

  return (
    <Jumbo>
      <div className="text-center text-2xl uppercase mb-4 text-purple">
        genesis doomcats auction {inAuctionNumber.toString()}
      </div>
      <div className="text-center mb-8">
        Ending in {d} days {h} hours {m} minutes {s} seconds
      </div>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-10">
        {catIds.length > 0 && catIds.map(catId => {
          return (
            <DoomCatAuctionCard tokenId={catId} key={catId} />
          )
        })}
      </div>
    </Jumbo>
  )
}

const CompletedAuction = ({ auctionNumber, loadedCallback }) => {
  const { catsPerAuction } = useData()

  const [catIds, setCatIds] = useState([])
  useEffect(() => {
    setCatIds(
      [...new Array(catsPerAuction.toNumber()).keys()]
        .map(i => {
          return i + 1 + BigNumber.from(auctionNumber - 1).mul(catsPerAuction).toNumber()
        })
    )
  }, [catsPerAuction, auctionNumber])

  const [catsLoaded, setCatsLoaded] = useState([])

  const catLoadedCallback = useCallback(catId => {
    setCatsLoaded(catsLoaded => {
      if (!catsLoaded.includes(catId.toString())) {
        return [...catsLoaded, catId.toString()]
      } else {
        return catsLoaded
      }
    })
  }, [])

  useEffect(() => {
    if (catsLoaded.length === catsPerAuction.toNumber() && loadedCallback) {
      loadedCallback(auctionNumber)
    }
  }, [auctionNumber, catsLoaded.length, catsPerAuction, loadedCallback])

  return (
    <div className="mb-8">
      <hr className="mb-8" />
      <div className="text-center text-xl mb-8">Auction {auctionNumber}</div>
      <div className="grid grid-cols-2 md:grid-cols-4 gap-10 mb-8">
        {catIds.length > 0 && catIds.map(catId => {
          return (
            <DoomCatAuctionCard tokenId={catId} key={catId} catLoadedCallback={catLoadedCallback} over />
          )
        })}
      </div>
    </div>
  )
}

const CompletedAuctions = () => {
  const { completedAuctions } = useData()

  const [auctionsOnScreen, setAuctionsOnScreen] = useState(1)
  const [auctionsSectionsToShow, setAuctionsSectionsToShow] = useState([])
  const [auctionsSections, setAuctionsSections] = useState([])
  const [auctionsLoaded, setAuctionsLoaded] = useState([])

  const bottomBoundaryRef = useRef(null)

  useEffect(() => {
    const observer = new IntersectionObserver(entries => {
      entries.forEach(en => {
        if (en.intersectionRatio > 0 && auctionsOnScreen === auctionsLoaded.length) {
          setAuctionsOnScreen(auctionsOnScreen => {
            if (auctionsOnScreen === completedAuctions.toNumber() || auctionsOnScreen > auctionsLoaded.length) {
              return auctionsOnScreen
            } else {
              return auctionsOnScreen + 1
            }
          })
        }
      })
    })

    if (bottomBoundaryRef.current) {
      observer.observe(bottomBoundaryRef.current)
    }

    const unobserveRef = bottomBoundaryRef.current

    return () => {
      if (unobserveRef) {
        observer.unobserve(unobserveRef)
      }
    }
  }, [auctionsLoaded, auctionsLoaded.length, auctionsOnScreen, completedAuctions])

  useEffect(() => {
    setAuctionsSectionsToShow(
      [...new Array(auctionsOnScreen).keys()]
        .map(auctionIndex => {
          return auctionIndex + completedAuctions.toNumber() - auctionsOnScreen
        })
    )
  }, [auctionsOnScreen, completedAuctions])

  const loadedCallback = useCallback(auctionId => {
    setAuctionsLoaded(auctionsLoaded => {
      if (!auctionsLoaded.includes(auctionId)) {
        return [...auctionsLoaded, auctionId]
      } else {
        return auctionsLoaded
      }
    })
  }, [])

  useEffect(() => {
    setAuctionsSections(auctionsSections => {
      const currentSectionsIds = auctionsSections.map(section => section.key)
      const missingSectionsIds = auctionsSectionsToShow.filter(id => !currentSectionsIds.includes(id.toString()))
      const newSections = missingSectionsIds.map(id => <CompletedAuction auctionNumber={id + 1} key={id} loadedCallback={loadedCallback} />)
      const allSections = [...auctionsSections, ...newSections]
      const sortedSections = allSections.sort((a, b) => Number(b.key) - Number(a.key))
      return sortedSections
    })
  }, [auctionsSectionsToShow, loadedCallback])

  return (
    <Jumbo>
      <div className="text-center text-2xl mb-8 text-purple uppercase">
        Completed Auctions
      </div>
      {auctionsSections.length > 0 && auctionsSections}
      <div ref={bottomBoundaryRef} />
    </Jumbo>
  )
}

const Auctions = () => {
  const { inAuctionNumber, completedAuctions, auctionsCount } = useData()

  return (
    <div>
      {inAuctionNumber.eq(0) && completedAuctions.lt(auctionsCount) && <NextAuctionIn />}
      {completedAuctions.eq(auctionsCount) && <AuctionsAreOver />}
      {inAuctionNumber.gt(0) && <CurrentAuction />}
      {completedAuctions.gt(0) && <CompletedAuctions />}
    </div>
  )
}

export default Auctions
