import { utils } from 'ethers'
import { Link } from 'react-router-dom'
import { useData } from '../dataContext'
import { useWeb3 } from '../web3'
import { connect } from '../web3/useProvider'
import { useState, useEffect } from 'react';

const Header = () => {
  const { myEtherBalance, myEns, hateBalance, showOpenSeaMessage } = useData()
  const { account } = useWeb3()
  const [userName, setUserName] = useState(null);
  
  useEffect(() => {
    const max = 10
    const ellipses = "..."

    if (myEns) {
      let name = myEns.split(".")[0].toUpperCase()
      if (name.length > max) {
        name = name.substring(0, max - ellipses.length) + ellipses
      }
      setUserName(name)
    } else if (account) {
      setUserName(account.substring(0, max))
    } else {
      setUserName(null)
    }
  }, [account, myEns]);
  
  return (
    <div className="w-full">
      <div className="bg-purple">
        <div className="container text-white py-6 flex flex-col sm:flex-row sm:justify-between lg:items-center">
          <div className="text-xl mb-6 sm:mb-0">
            <Link to="/">DoomCatRescue</Link>
          </div>
          <div className="flex flex-col lg:flex-row text-right">
            <Link to="/ascended" className="mb-2 lg:mb-0 lg:mr-4">ASCENDED</Link>
            <div className="hidden lg:block lg:mr-4">|</div>
            <Link to="/auctions" className="mb-2 lg:mb-0 lg:mr-4">AUCTIONS</Link>
            <div className="hidden lg:block lg:mr-4">|</div>
            
            {userName
              ? <Link to="/gallery">{userName}</Link>
              : <button className="hover:underline ml-auto" onClick={() => connect()}>CONNECT</button>
            }
          </div>
        </div>
      </div>
      <div className="container">
        {account && <div className="text-white text-sm pt-4 -mb-8 flex justify-between">
          <div>
            {showOpenSeaMessage && <div>
              <Link to="/open-sea-approval">OpenSea Approval</Link>
            </div>}
          </div>
          <div className="text-right flex">
            <div className="mr-6">
              <div>{parseFloat(utils.formatEther(myEtherBalance)).toFixed(4)} ETH  </div>
            </div>
            <div>
              <div>{parseFloat(utils.formatEther(hateBalance)).toFixed(4)} HATE</div>
            </div>
          </div>
        </div>}
        <div className="my-16 flex flex-col">
          <h1 className="text-purple text-shadow text-xl sm:text-4xl md:text-5xl lg:text-6xl text-center mb-2">DoomCatRescue</h1>
          <div className="text-center text-white text-xs mt-2">
            <a href="https://discord.com/invite/P42tGfnh3z" target="_blank" rel="noreferrer">Discord</a> | <a href="https://twitter.com/doom_cats" target="_blank" rel="noreferrer">Twitter</a>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Header
