import { useState, useEffect } from 'react'

const supportedChains = () => {
  const dev = process.env.NODE_ENV !== 'production' ? [parseInt(process.env.REACT_APP_LOCAL_CHAIN_ID, 10)] : []
  return [...dev, parseInt(process.env.REACT_APP_CHAIN_ID, 10)]
}

const useAddresses = chainId => {
  const [addresses, setAddresses] = useState()

  useEffect(() => {
    if (chainId === parseInt(process.env.REACT_APP_LOCAL_CHAIN_ID, 10)) {
      setAddresses({
        doomCatRescue: process.env.REACT_APP_LOCAL_DOOMCAT_ADDRESS,
      })
    } else if (chainId === parseInt(process.env.REACT_APP_CHAIN_ID, 10)) {
      setAddresses({
        doomCatRescue: process.env.REACT_APP_DOOMCAT_ADDRESS,
      })
    }
  }, [chainId])

  return addresses
}

export {
  supportedChains,
  useAddresses
}
