import { useState, useEffect } from 'react'
import { constants } from 'ethers'

function useOpenSeaProxyAddress(proxyRegistry, account, blockNum) {
  const [openSeaProxyAddress, setOpenSeaProxyAddress] = useState(constants.AddressZero)

  useEffect(() => {
    if (!proxyRegistry || !account) {
      setOpenSeaProxyAddress(constants.AddressZero)
      return
    }

    proxyRegistry.proxies(account)
      .then(setOpenSeaProxyAddress)
      .catch(console.error)
  }, [account, proxyRegistry, blockNum])

  return openSeaProxyAddress
}

function useOpenSeaApproved(doomCatContract, account, balance, proxyAddress, blockNum) {
  const [openSeaApproved, setOpenSeaAppproved] = useState(false)

  useEffect(() => {
    if (!doomCatContract || !account || balance.eq(0) || proxyAddress === constants.AddressZero) {
      setOpenSeaAppproved(false)
      return
    }

    doomCatContract.tokenOfOwnerByIndex(account, 0)
      .then(tokenId => doomCatContract.callStatic.transferFrom(account, doomCatContract.address, tokenId, { from: proxyAddress }))
      .then(() => setOpenSeaAppproved(true))
      .catch(() => setOpenSeaAppproved(false))
  }, [account, doomCatContract, balance, proxyAddress, blockNum])

  return openSeaApproved
}

function useShowOpenSeaMessage(account, myCatBalance, openSeaApproved) {
  const [showOpenSeaMessage, setShowOpenSeaMessage] = useState(false)

  useEffect(() => {
    setShowOpenSeaMessage(
      account &&
      myCatBalance.gt(0) &&
      !openSeaApproved
    )
  }, [account, myCatBalance, openSeaApproved])

  return showOpenSeaMessage
}

export {
  useOpenSeaProxyAddress,
  useOpenSeaApproved,
  useShowOpenSeaMessage,
}