import { useWeb3 } from '../web3'
import { useData } from '../dataContext'
import { utils } from 'ethers'
import { Link } from 'react-router-dom'

const AuctionDetails = ({ catInfo, inAuction }) => {
  const { account } = useWeb3()
  const { auctionsBidIncrement } = useData()

  return(
    <div className="mt-1">
      {inAuction && catInfo.highBidOwner.toLowerCase() === account.toString().toLowerCase() &&<div>
        <div className="mb-4"> You Are In The Top Bid! </div>
      </div>}

      {inAuction && <div className="opacity-50">
        <div className="mb-1">Min Bid Increment: {utils.formatEther(auctionsBidIncrement)} ETH</div>
        <div className="mb-1">Minimum Next Bid: {utils.formatEther(catInfo.highBidAmount.add(auctionsBidIncrement))} ETH</div>
      </div>}

      <div className="mb-1">Top Bidder: <Link className="text-purple" to={`/gallery/${catInfo.highBidOwner}`}>{catInfo.highBidOwner.substring(0,8)}</Link></div>
      <div className="mb-1">Top Bid: {utils.formatEther(catInfo.highBidAmount)} ETH</div>
      <div className="mb-1">My Bid: {utils.formatEther(catInfo.bidByAddress.toString())} ETH</div>
      <div className="mb-1">Total Bids: {catInfo.bidCount.toNumber()}</div>
      
    </div>
  )
}

export default AuctionDetails
