import { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom';
import { BigNumber, utils } from 'ethers'
import { useData } from '../dataContext'
import Jumbo from '../components/Jumbo'
import { useWeb3 } from '../web3'
import { useTransaction } from '../dataContext/transactions'
import { useTimeSplitter } from '../utils/time'

const ProgressBar = () => {
  const { totalCats, genesisCats, remainingCats } = useData()

  const [totalRescuable, setTotalRescuable] = useState(BigNumber.from(0))
  useEffect(() => {
    if (!totalCats || !genesisCats) return
    setTotalRescuable(totalCats.sub(genesisCats))
  }, [totalCats, genesisCats])

  const [rescuedCount, setRescuedCount] = useState(BigNumber.from(0))
  useEffect(() => {
    if (!remainingCats || !totalCats || !genesisCats) return
    setRescuedCount(totalCats.sub(remainingCats).sub(genesisCats))
  }, [genesisCats, remainingCats, totalCats])

  const [percentageRescued, setPercentageRescued] = useState(0)
  useEffect(() => {
    setPercentageRescued(Math.trunc(rescuedCount.toNumber() / totalRescuable.toNumber() * 100 * 10 ) / 10)
  }, [rescuedCount, totalRescuable])

  return (
    <div className="rounded-2xl border-4 overflow-hidden text-xs">
      <div className="p-1 text-white bg-purple overflow-hidden" style={{ width: `${percentageRescued}%` }}>
        {percentageRescued} %
      </div>
    </div>
  )
}

const Rescue = () => {
  const {
    doomCatContract,
    currentTime,
    rescueStartTime,
    remainingCats,
    rescuePrice, rescuePriceIncrement,
    rescueRateLimit, currentBlock, rescueLastBlock,
  } = useData()
  const { account } = useWeb3()
  const { contractCall, pending } = useTransaction()
  const history = useHistory();

  const [buttonDisabled, setButtonDisabled] = useState(true)
  useEffect(() => {
    setButtonDisabled(
      !(
        !pending &&
        account &&
        currentTime.gte(rescueStartTime) &&
        currentBlock.gte(rescueLastBlock.add(rescueRateLimit))
      )
    )
  }, [account, currentBlock, currentTime, pending, rescueLastBlock, rescueRateLimit, rescueStartTime])

  const rescue = () => {
    contractCall(
      doomCatContract.rescue, [{ value: rescuePrice.add(rescuePriceIncrement), gasLimit: 400000 }], // send a lil extra
      "ASCENDING DOOMCAT", "ASCENSION FAILED", "ASCENSION SUCCESSFUL", (receipt) => {
        history.push(`/ascended/${receipt.events[1].args.tokenId.toString()}`)
      }
    )
  }

  const { d, h, m, s } = useTimeSplitter(rescueStartTime.sub(currentTime))

  const [localRemainingCats, setLocalRemainingCats] = useState(BigNumber.from(0))
  useEffect(() => {
    if (remainingCats) {
      setLocalRemainingCats(remainingCats)
    }
  }, [remainingCats])

  return (
    <Jumbo>
      <div className="text-center">
        <h4>DoomCats Remaining: {localRemainingCats.toString()}</h4>
        {currentTime.lt(rescueStartTime) && <div><h4 className="mt-4 uppercase">
          DoomCat Ascension begins in
        </h4><h3>{d} days {h} hours {m} minutes {s} seconds</h3></div>}
        {localRemainingCats.gt(0) &&
          <div className="mt-4">
            <h4 className="mb-4">Price: {parseFloat(utils.formatEther(rescuePrice.toString())).toFixed(5)} ETH</h4>
            <div className="mb-4">
              <ProgressBar />
            </div>
            <button disabled={buttonDisabled} onClick={rescue} className="bg-purple text-white px-4 py-2 shadow-lg disabled:cursor-not-allowed disabled:opacity-30">
              RESCUE
            </button>
          </div>
        }
      </div>
    </Jumbo>
  )
}

export default Rescue
