import { ToastContainer } from 'react-toastify'
import Header from './components/header';
import Footer from './components/footer';
import Home from './pages/Home'
import DoomCatDetails from './pages/DoomCatDetails'
import Auctions from './pages/Auctions'
import AuctionCatDetails from './pages/AuctionCatDetails'
import Ascended from './pages/Ascended'
import Gallery from './pages/Gallery'
import { Web3Provider } from './web3'
import { DataProvider } from './dataContext'
import {
  HashRouter as Router,
  Route,
  Switch,
} from 'react-router-dom';
import OpenSeaApproval from './pages/OpenSeaApproval';

function App() {
  return (
    <Web3Provider>
      <DataProvider>
        <Router>
          <div className="min-h-screen flex flex-col justify-between">
            <div>
              <Header />
              <div className="container">
                <Switch>
                  <Route path="/ascended/:id" component={DoomCatDetails} />
                  <Route path="/ascended" component={Ascended} />
                  <Route path="/auctions/:id" component={AuctionCatDetails} />
                  <Route path="/auctions" component={Auctions} />
                  <Route path="/gallery/:addressParam" component={Gallery} />
                  <Route path="/gallery" component={Gallery} />
                  <Route path="/open-sea-approval" component={OpenSeaApproval} />
                  <Route path="/" component={Home} />
                </Switch>
              </div>
            </div>
            <Footer />
          </div>
        </Router>
        <ToastContainer
          closeButton={false}
          position="bottom-right"
          newestOnTop={false}
        />
      </DataProvider>
    </Web3Provider>
  );
}

export default App;

