import { Link } from 'react-router-dom'
import Image from 'react-graceful-image'
import { useData } from '../dataContext'
import { useTokenURI, useCatInfo } from '../dataContext/cats'
import { useEffect } from 'react'

const Container = ({ children }) => {
  return (
    <div className="transition duration-500 ease-in-out transform hover:scale-110">
      {children}
    </div>
  )
}

const LoadingError = ({ children, catId }) => {
  return (
    <div className="h-full text-center text-sm p-4">
      <div>DoomCat #{catId.toString()}</div>
      <div className="flex flex-col justify-center mt-6">{children}</div>
    </div>
  )
}

const DoomCatCard = ({ tokenId, loadedCallback }) => {
  const { doomCatContract } = useData()
  const tokenURI = useTokenURI(doomCatContract, tokenId)
  const [catInfo, error] = useCatInfo(doomCatContract, tokenId, tokenURI)

  useEffect(() => {
    if (catInfo && loadedCallback) {
      loadedCallback(tokenId)
    }
  }, [catInfo, loadedCallback, tokenId])

  if (error) {
    return (
      <Container>
        <LoadingError catId={tokenId}>
          <div>Error: {error.message}</div>
        </LoadingError>
      </Container>
    )
  } else if (catInfo) {
    return (
      <Link to={{
        pathname: `/ascended/${tokenId}`,
        state: { catInfo }
      }}>
        <Container>
          <div className="text-center text-sm mb-2">{catInfo.name}</div>
          <div className="text-sm">
            <Image
              src={catInfo.image}
              retry={{ count: 30, delay: 3 }}
              width="100%"
              alt={catInfo.name}
            />
          </div>
        </Container>
      </Link>
    )
  } else {
    return (
      <Container>
        <LoadingError catId={tokenId}>
          <div>Loading...</div>
        </LoadingError>
      </Container>
    )
  }
}

export default DoomCatCard
