import { useEffect, useState } from 'react'
import Image from 'react-graceful-image'
import { BigNumber, constants, utils } from 'ethers'
import { Link, Redirect, useParams, useLocation } from 'react-router-dom'
import Jumbo from "../components/Jumbo"
import OpenSeaMessageAccount from '../components/OpenSeaMessageAccount'
import { useWeb3 } from '../web3'
import { useData } from '../dataContext'
import { useTokenURI, useCatInfo, useCatOwner } from '../dataContext/cats'
import { useTransaction } from '../dataContext/transactions'

const LoadingError = ({ children, catId }) => {
  return (
    <Jumbo>
      <div className="h-full text-center">
        <div className="text-xl">DoomCat #{catId.toString()}</div>
        <div className="flex flex-col justify-center h-full p-4">{children}</div>
      </div>
    </Jumbo>
  )
}

const Rename = ({ catId }) => {
  const { hateAllowance, hateBalance, nameChangePrice, doomCatContract, hateContract } = useData()
  const { contractCall, pending } = useTransaction()
  const [callData, setCallData] = useState([])
  const [buttonText, setButtonText] = useState("APPROVE")
  const [name, setName] = useState("")
  const [buttonDisabled, setButtonDisabled] = useState(true)

  useEffect(() => {
    setButtonDisabled(
      !(
        !pending &&
        hateBalance.gte(nameChangePrice)
      )
    )
  }, [pending, hateBalance, nameChangePrice])

  useEffect(() => {
    if (hateAllowance.lt(nameChangePrice)) {
      if (!hateContract) return
      setButtonText("APPROVE")
      setCallData([
        hateContract.approve,
        [doomCatContract.address, constants.MaxUint256],
        "APPROVING HATE", "HATE APPROVAL FAILED", "HATE APPROVAL SUCCEEDED",
        () => {
          setCallData([])
        }
      ])
    } else {
      if (!doomCatContract) return
      setButtonText("RENAME")
      setCallData([
        doomCatContract.nameCat,
        [catId, name],
        "NAMING DOOMCAT", "NAMING FAILED", "NAMING SUCCESSFUL",
        () => {
          setName("")
          setCallData([])
        }
      ])
    }
  }, [catId, doomCatContract, hateAllowance, hateContract, name, nameChangePrice])

  const nameCat = e => {
    e.preventDefault()
    contractCall(...callData)
  }

  return (
    <form className="-mt-4 w-full" onSubmit={nameCat}>
      <div className="text-xs text-center mb-2">
        Name your DoomCat for {utils.formatEther(nameChangePrice)} HATE
      </div>
      <div className="flex flex-col sm:flex-row items-center">
        <input type="text" className="flex-grow border rounded p-2 w-min mb-4 sm:mr-4 sm:mb-0" value={name} onChange={e => setName(e.target.value)} />
        <button type="submit" disabled={buttonDisabled} className="bg-purple text-white px-4 py-2 shadow-lg disabled:cursor-not-allowed disabled:opacity-30">{buttonText}</button>
      </div>
    </form>
  )
}

const DoomCatDetails = () => {
  const { id } = useParams()
  const [catId, setCatId] = useState(BigNumber.from(0))
  const { doomCatContract } = useData()
  const { account } = useWeb3()

  const location = useLocation()
  const tokenURI = useTokenURI(doomCatContract, catId)
  const [catInfo, error] = useCatInfo(doomCatContract, catId, tokenURI, location?.state?.catInfo)
  const owner = useCatOwner(doomCatContract, catId)

  useEffect(() => {
    if (!isNaN(id)) {
      setCatId(BigNumber.from(id))
    } else {
      setCatId(null)
    }
  }, [id])

  const [canName, setCanName] = useState(false)
  useEffect(() => {
    if (!catInfo) return
    setCanName(account && account.toLowerCase() === owner.toLowerCase() && !catInfo.isNamed)
  }, [account, catInfo, owner])

  if (catId === null) {
    return <Redirect to="/" />
  } else if (error) {
    return (
      <LoadingError catId={catId}>
        <div>Error: {error.message}</div>
      </LoadingError>
    )
  } else if (catInfo) {
    return (
      <Jumbo>
        <div className="mb-4 text-center text-xl">{catInfo.name}</div>
        <div className="mb-8 text-center text-lg">
          Owned by <Link className="text-purple" to={`/gallery/${owner}`}>{owner.substring(0, 8)}</Link>
        </div>
        {canName && <Rename catId={catId} />}
        <div className="text-purple text-center mt-4 flex justify-center text-xs items-center">
          <a href={`https://opensea.io/assets/${doomCatContract?.address}/${catInfo.id}`} target="_blank" rel="noreferrer">View on OpenSea</a>
          <div className="mx-2">|</div>
          <a href={`https://rarible.com/token/${doomCatContract?.address}:${catInfo.id}`} target="_blank" rel="noreferrer">View on Rarible</a>
        </div>
        <div className="text-xs mt-4">
          <div className="max-w-md mx-auto">
            <Image
              src={catInfo.image}
              retry={{ count: 30, delay: 3 }}
              width="100%"
              alt={catInfo.name}
            />
          </div>
          <div className="flex flex-col items-center mt-4">
            <div>
              <div>ID: {catInfo.id}</div>
              {catInfo.attributes.map((a, i) => {
                return (
                  <div key={i}>{a.trait_type}: {a.value}</div>
                )
              })}
            </div>
          </div>
          <div className="mt-8 text-center">
            <OpenSeaMessageAccount accountCompare={owner} />
          </div>
        </div>
      </Jumbo>
    )
  } else {
    return (
      <LoadingError catId={catId}>
        <div>Loading...</div>
      </LoadingError>
    )
  }
}

export default DoomCatDetails
