import { useState, useEffect } from 'react'
import { BigNumber, constants, utils } from 'ethers'
import { useData } from '../dataContext'
import { useWeb3 } from '../web3'
import { useTransaction } from '../dataContext/transactions'
import { useCatOwner } from '../dataContext/cats'

const Bid = ({ catInfo }) => {
  const { contractCall, pending } = useTransaction()
  const [callData, setCallData] = useState([])
  const [buttonDisabled, setButtonDisabled] = useState(true)

  const { doomCatContract, auctionsBidIncrement, catsPerAuction, myEtherBalance } = useData()
  const [bidAmount, setBidAmount] = useState("0")
  const [minBidAmount, setMinBidAmount] = useState(BigNumber.from(0));
  const [bidAmountBigNumber, setBidAmountBigNumber] = useState(BigNumber.from(0));

  const updateBid = e => {
    const value = e.target.value.split(".")

    if (value.length === 1) {
      setBidAmount(value[0])
      return
    }

    if (value.length === 2) {
      const decimals = value[1].substring(0, 18)
      setBidAmount(`${value[0]}.${decimals}`)
      return
    }

    // shouldn't get here
    setBidAmount("0")
  }

  useEffect(() => {
    setMinBidAmount(catInfo.highBidAmount.add(auctionsBidIncrement))
  }, [auctionsBidIncrement, catInfo])


  useEffect(() => {
    if (!bidAmount) return
    setBidAmountBigNumber(utils.parseEther(bidAmount).sub(catInfo.bidByAddress))
  }, [bidAmount, catInfo.bidByAddress])

  useEffect(() => {
    setBidAmount(bidAmount => {
      if (utils.parseEther(bidAmount).lt(minBidAmount)) {
        return utils.formatEther(minBidAmount)
      } else {
        return bidAmount
      }
    })
  }, [minBidAmount])

  useEffect(() => {
    setButtonDisabled(
      !(
        !pending &&
        bidAmountBigNumber.add(catInfo.bidByAddress).gte(minBidAmount) &&
        myEtherBalance.gte(bidAmountBigNumber)
      )
    )
  }, [pending, catInfo, bidAmountBigNumber, minBidAmount, myEtherBalance])

  useEffect(() => {
    if (!doomCatContract) return

    setCallData([
      doomCatContract.bid,
      [((catInfo.id % catsPerAuction.toNumber()) - 1), { value: bidAmountBigNumber.toString() }],
      "PLACING BID", "BID FAILED", "BID SUCCEEDED",
    ])
  }, [auctionsBidIncrement, bidAmountBigNumber, catInfo, catsPerAuction, doomCatContract])


  const bid = e => {
    e.preventDefault()
    contractCall(...callData)
  }

  return (
    <form className="mb-4" onSubmit={bid}>
      <div className="flex flex-col sm:flex-row items-center">
        <input type="number" step="any" min={utils.formatEther(minBidAmount)} placeholder={utils.formatEther(minBidAmount)} className="flex-grow border rounded p-2 w-min mb-4 sm:mr-4 sm:mb-0" value={bidAmount} onChange={updateBid} />
        <button type="submit" disabled={buttonDisabled} className="bg-purple text-white px-4 py-2 shadow-lg disabled:cursor-not-allowed disabled:opacity-30">BID</button>
      </div>
    </form>
  )
}

const Withdraw = ({ catInfo }) => {
  const { contractCall, pending } = useTransaction()
  const [buttonDisabled, setButtonDisabled] = useState(true)
  const { doomCatContract } = useData()

  useEffect(() => {
    setButtonDisabled(pending)
  }, [pending])

  const withdraw = () => {
    contractCall(
      doomCatContract.withdrawLowBid,
      [catInfo.id],
      "WITHDRAW BID", "WITHDRAW FAILED", "WITHDRAW SUCCEEDED"
    )
  }

  const [buttonText, setButtonText] = useState("WITHDRAW BID")
  useEffect(() => {
    setButtonText(`WITHDRAW ${utils.formatEther(catInfo.bidByAddress)} ETH BID`)
  }, [catInfo.bidByAddress])

  return (
    <div className="text-center mb-4">
      <button type="submit" disabled={buttonDisabled} onClick={withdraw} className="bg-purple text-white px-4 py-2 shadow-lg disabled:cursor-not-allowed disabled:opacity-30">{buttonText}</button>
    </div>
  )
}

const Claim = ({ catInfo }) => {
  const { contractCall, pending } = useTransaction()
  const [buttonDisabled, setButtonDisabled] = useState(true)
  const { doomCatContract } = useData()

  useEffect(() => {
    setButtonDisabled(pending)
  }, [pending])

  const claim = () => {
    contractCall(
      doomCatContract.claimWinningBid,
      [catInfo.id],
      "CLAIMING GENESIS CAT", "CLAIM FAILED", "CLAIM SUCCEEDED"
    )
  }

  return (
    <div className="text-center mb-4">
      <button type="submit" disabled={buttonDisabled} onClick={claim} className="bg-purple text-white px-4 py-2 shadow-lg disabled:cursor-not-allowed disabled:opacity-30">{" CLAIM GENESIS CAT "}</button>
    </div>
  )
}

const AuctionInputs = ({ catInfo, inAuction }) => {
  const { account } = useWeb3()

  const [canBid, setCanBid] = useState(false)
  useEffect(() => {
    setCanBid(inAuction)
  }, [inAuction])

  const [canWithdraw, setCanWithdraw] = useState(false)
  useEffect(() => {
    if (!account) return

    setCanWithdraw(
      catInfo.bidByAddress.gt(0) &&
      catInfo.highBidOwner.toLowerCase() !== account.toLowerCase()
    )
  }, [account, catInfo.bidByAddress, catInfo.highBidOwner])
  
  const [catId, setCatId] = useState(BigNumber.from(0))
  useEffect(() => {
    if (!catInfo.id) return
    setCatId(BigNumber.from(catInfo.id))
  }, [catInfo])

  const { doomCatContract } = useData()
  const owner = useCatOwner(doomCatContract, catId)
  const [canClaim, setCanClaim] = useState(false)
  useEffect(() => {
    if (!account) return

    setCanClaim(
      !inAuction &&
      catInfo.highBidOwner.toLowerCase() === account.toLowerCase() &&
      owner === constants.AddressZero
    )
  }, [account, catInfo.highBidOwner, inAuction, owner])

  return (
    <div>
      {canBid && <Bid catInfo={catInfo} />}
      {canWithdraw && <Withdraw catInfo={catInfo} />}
      {canClaim && <Claim catInfo={catInfo} />}
    </div>
  )
}

export default AuctionInputs
