import { useState, useEffect } from 'react'
import { BigNumber } from 'ethers'

function useGenesisCats(doomCatRescue) {
  const [genesisCats, setGenesisCats] = useState(null)

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.genesisCats()
      .then(setGenesisCats)
      .catch(console.error)
  }, [doomCatRescue])

  return genesisCats
}


