function useCompletedAuctions(currentTime, start, duration, distance, count) {
  const [completedAuctions, setCompletedAuctions] = useState(BigNumber.from(0))

  useEffect(() => {
    const endOfFirst = start.add(duration)
    const fullCycle = duration.add(distance)

    // first auction hasn't completed yet
    if (currentTime.lt(endOfFirst)) {
      setCompletedAuctions(BigNumber.from(0))
      return
    }

    // last auction is over
    if (currentTime.gte(endOfFirst.add(fullCycle.mul(count)))) {
      setCompletedAuctions(count)
      return
    }

    // in the auction cycle
    setCompletedAuctions(currentTime.sub(endOfFirst).div(fullCycle).add(1))
  }, [count, currentTime, distance, duration, start])

  return completedAuctions
}

function useAuctionEndsIn(currentTime, start, duration, distance, count) {
  const [auctionEndsIn, setAuctionEndsIn] = useState(BigNumber.from(0))

  useEffect(() => {
    if (duration.eq(0)) return

    const fullCycle = duration.add(distance)
    const remaining = duration.sub(currentTime.sub(start).mod(fullCycle))

    // before first auction starts
    if (currentTime.lt(start)) {
      setAuctionEndsIn(BigNumber.from(0))
      return
    }

    // after last auction starts
    if (currentTime.gte(start.add(fullCycle.mul(count)))) {
      setAuctionEndsIn(BigNumber.from(0))
      return
    }

    // during auctions
    if (remaining.lt(0)) {
      setAuctionEndsIn(BigNumber.from(0))
    } else {
      setAuctionEndsIn(remaining)
    }

  }, [count, currentTime, distance, duration, start])

  return auctionEndsIn
}

function useNextAuctionIn(currentTime, start, duration, distance, count) {
  const [nextAuctionIn, setNextAuctionIn] = useState(BigNumber.from(0))

  useEffect(() => {
    if (duration.eq(0)) return

    const fullCycle = duration.add(distance)
    const remaining = distance.sub(currentTime.sub(start).add(distance).mod(fullCycle))

    // before first auction starts
    if (currentTime.lt(start)) {
      setNextAuctionIn(start.sub(currentTime))
      return
    }

    // after last auction starts
    if (currentTime.gte(start.sub(duration).add(fullCycle.mul(count)))) {
      setNextAuctionIn(BigNumber.from(0))
      return
    }

    // during auctions
    if (remaining.lt(0)) {
      setNextAuctionIn(BigNumber.from(0))
    } else {
      setNextAuctionIn(remaining)
    }
  }, [count, currentTime, distance, duration, start])

  return nextAuctionIn
}

function useInAuctionNumber(currentTime, start, duration, distance, count) {
  const [inAuctionNumber, setInAuctionNumber] = useState(BigNumber.from(0))

  useEffect(() => {
    if (duration.eq(0)) return
    
    const fullCycle = duration.add(distance)
    const remaining = distance.sub(currentTime.sub(start).add(distance).mod(fullCycle))

    // first auction hasn't started yet
    if (currentTime.lt(start)) {
      setInAuctionNumber(BigNumber.from(0))
      return
    }

    // last auction is over
    if (currentTime.gte(start.sub(duration).add(fullCycle.mul(count)))) {
      setInAuctionNumber(BigNumber.from(0))
      return
    }

    // in the auction cycle
    if (remaining.gt(0)) {
      setInAuctionNumber(BigNumber.from(0))
    } else {
      setInAuctionNumber(currentTime.sub(start).div(fullCycle).add(1))
    }
    
  }, [count, currentTime, distance, duration, start])

  return inAuctionNumber
}

export {
  useGenesisCats,
  useAuctionsStart,
  useAuctionsDuration,
  useAuctionsDistance,
  useAuctionsCount,
  useAuctionsCatsPerAuction,
  useAuctionsBidIncrement,
  useCompletedAuctions,
  useAuctionEndsIn,
  useNextAuctionIn,
  useInAuctionNumber,
}
