import { utils } from 'ethers'
import { useParams, useLocation, Redirect } from 'react-router-dom'
import Jumbo from "../components/Jumbo"
import DoomCatCard from '../components/DoomCatCard'
import OpenSeaMessageAccount from '../components/OpenSeaMessageAccount'
import { useWeb3 } from '../web3'
import { useData } from '../dataContext'
import { useCatBalance, useTokensOfOwnerByIndexes } from '../dataContext/cats'

const Gallery = () => {
  const { addressParam } = useParams()
  const { account } = useWeb3()
  const location = useLocation()

  const { doomCatContract } = useData()
  const catBalance = useCatBalance(doomCatContract, addressParam)
  const catIds = useTokensOfOwnerByIndexes(doomCatContract, addressParam, catBalance)

  if (!addressParam) {
    if (!account) {
      return <Redirect to="/" />
    } else {
      return <Redirect to={`${location.pathname}/${account}`} />
    }
  } else if (!utils.isAddress(addressParam)) {
    return <Redirect to="/" />
  } else {
    return (
      <Jumbo>
        <div className="mb-8 text-center text-xl italic">
          <a className="text-purple" href={`https://etherscan.io/token/${doomCatContract?.address}?a=${addressParam}`} target="_blank" rel="noreferrer">{addressParam.substring(0, 8)}</a> has {catBalance.toString()} DoomCats
          <OpenSeaMessageAccount accountCompare={addressParam} />
        </div>
        <div className="grid grid-cols-2 md:grid-cols-4 gap-10">
          {catIds.length > 0 && catIds.map(catId => {
            return (
              <DoomCatCard tokenId={catId} key={catId.toNumber()} />
            )
          })}
        </div>
      </Jumbo>
    )
  }
}

export default Gallery
