import { useState, useEffect } from 'react'
import { BigNumber } from 'ethers'

function useMyHateBalance(account, hate, blockNum) {
  const [myHateBalance, setMyHateBalance] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!account || !hate) return

    hate.balanceOf(account)
      .then(setMyHateBalance)
      .catch(console.error)
  }, [account, hate, blockNum])

  return myHateBalance
}

function useNameChangePrice(doomCatRescue) {
  const [nameChangePrice, setNameChangePrice] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.nameChangePrice()
      .then(setNameChangePrice)
      .catch(console.error)
  }, [doomCatRescue])

  return nameChangePrice
}

function useHateAllowance(doomCatRescue, hate, account, blockNum) {
  const [allowance, setAllowance] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue || !hate || !account) return

    hate.allowance(account, doomCatRescue.address)
      .then(setAllowance)
      .catch(console.error)
  }, [account, doomCatRescue, hate, blockNum])

  return allowance
}

export {
  useMyHateBalance,
  useNameChangePrice,
  useHateAllowance,
}
