import { useState, useEffect } from 'react'
import { BigNumber, utils } from 'ethers'
import Jumbo from '../components/Jumbo'
import Rescue from '../components/Rescue'
import { useData } from '../dataContext'

const Paragraph = ({ children }) => {
  return (
    <div className="mb-8">
      {children}
    </div>
  )
}

const Home = () => {
  const {
    totalCats, genesisCats,
    auctionsCount, catsPerAuction, auctionsBidIncrement,
    rescueStartTime, rescuePriceInitial, rescuePriceIncrement, rescueTrancheSize, rescuePriceFinal,
  } = useData()

  const startDate = new Date(rescueStartTime.toNumber() * 1000)

  const [localTotalCats, setLocalTotalCats] = useState(BigNumber.from(0))
  useEffect(() => {
    if (totalCats) {
      setLocalTotalCats(totalCats)
    }
  }, [totalCats])

  const [localGenesisCats, setLocalGenesisCats] = useState(BigNumber.from(0))
  useEffect(() => {
    if (genesisCats) {
      setLocalGenesisCats(genesisCats)
    }
  }, [genesisCats])

  return (
    <div>
      <div>
        <Rescue />
        <Jumbo>
          <div className="mb-8 text-center text-xl">RED ALERT</div>
          <Paragraph>
            Crew Member,
          </Paragraph>
          <Paragraph>
            Congratulations on your ascension.
          </Paragraph>
          <Paragraph>
            Now, you must save the DoomCats.
          </Paragraph>
          <Paragraph>
            There are {localTotalCats.toString()} DoomCats trapped down on Earth unable to reach the next plane of existence.
          </Paragraph>

          <div className="mb-8 text-xl">How can I rescue the DoomCats?</div>
          <Paragraph>
            On {startDate.toLocaleDateString()} at {startDate.toLocaleTimeString()}, transportation for the DoomCat souls will be possible through an affordable, linear bonding curve starting at {utils.formatEther(rescuePriceInitial)} ETH, increasing by {utils.formatEther(rescuePriceIncrement)} ETH every {rescueTrancheSize.toNumber()} cats sold, to a cap of {utils.formatEther(rescuePriceFinal)} ETH.
          </Paragraph>
          <Paragraph>
            Hitting the RESCUE button will open your wallet (e.g. MetaMask or a WalletConnect compatible wallet), allowing you to start the process of saving your DoomCats soul.
          </Paragraph>

          <div className="mb-8 text-xl">What happens when I rescue a DoomCat?</div>
          <Paragraph>
            Once ascended, you receive an NFT of the DoomCats soulan ERC-721 token. Each token is a unique 24x24 image varying in pose, direction, or color.
          </Paragraph>
          <Paragraph>
            94% of the DoomCats are strays, grateful to have their ethereal selves elevated to cosmic travel.
          </Paragraph>
          <Paragraph>
            The highest ascended catsthe 6% of most spiritually evolved Cats are given unique breeds (Glowie, Alien, Zombie, Nuke, Hate, or Smokey).
          </Paragraph>
          <Paragraph>
            5% of DoomCats have a chance for their journey to be backdropped with a special background, including: Desert, Beach, Windows, Matrix, or Heaven.
          </Paragraph>
          <Paragraph>
            2% of DoomCats brought their favorite item to ease the graduation process: items include: googles, ciggies, or one of five colored party hats. Party hat colors include: red, blue, yellow, purple, or white.
          </Paragraph>
          <Paragraph>
            There are 39 purple-draped cats in honor of the first 39 ascended Members. Additionally, the oldest 0.1% of souls will be completely black and white, the Genesis DoomCats
          </Paragraph>

          <div className="mb-8 text-xl">How are Genesis DoomCats rescued?</div>
          <Paragraph>
            The {localGenesisCats.toString()} Genesis DoomCats will be distributed in a fully unique way {auctionsCount.toString()} total weekly auctions with {catsPerAuction.toString()} Genesis DoomCats auctioned every week. Auctions start at {parseFloat(utils.formatEther(auctionsBidIncrement.toString())).toFixed(2)} ETH per cat. That means no bots front-running, no dev allocation, no other way to claim a Genesis DoomCat than win with the highest bid.
          </Paragraph>
          <Paragraph>
            This distribution mechanic is the first of its kind, unlike any other in the Universe.
          </Paragraph>
          
          <div className="mb-8 text-xl">What happens to the funds used for DoomCat Rescue?</div>
          <Paragraph>
            ETH from the initial distribution, and the {auctionsCount.toNumber()} auctions, will be added to the HATE Uniswap LP and burned to the next plane of spiritual existence.
          </Paragraph>

          <div className="mb-8 text-xl">Can I interact with my DoomCat?</div>
          <Paragraph>
            DoomCat Rescue allows users to spend HATE to name their DoomCats.
          </Paragraph>

          <hr className="mb-8" />
          <Paragraph>
            The Present Representative,<br />
            &nbsp;&nbsp;&nbsp;&nbsp;Marshall Applewhite
          </Paragraph>
        </Jumbo>
      </div>
    </div>
  )
}

export default Home
