import { useState, useEffect } from 'react'
import { BigNumber } from 'ethers'

function useRescueStartTime(doomCatRescue) {
  const [rescueStartTime, setRescueStartTime] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.rescueStartTime()
      .then(setRescueStartTime)
      .catch(console.error)
  }, [doomCatRescue])

  return rescueStartTime
}

function useTotalCats(doomCatRescue) {
  const [totalCats, setTotalCats] = useState(null)

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.totalCats()
      .then(setTotalCats)
      .catch(console.error)
  }, [doomCatRescue])

  return totalCats
}

function useRemainingCats(doomCatRescue) {
  const [remainingCats, setRemainingCats] = useState(null)

  useEffect(() => {
    if (!doomCatRescue) return

    function getRemainingCats() {
      doomCatRescue.remainingCats()
        .then(setRemainingCats)
        .catch(console.error)
    }
    
    getRemainingCats()

    function updateRemainingCats() {
      getRemainingCats()
    }

    doomCatRescue.on('Rescue', updateRemainingCats)

    return () => {
      doomCatRescue.removeListener('Rescue', updateRemainingCats)
    }
  }, [doomCatRescue])

  return remainingCats
}

