function useRescueTrancheSize(doomCatRescue) {
  const [rescueTrancheSize, setRescueTrancheSize] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.rescueTrancheSize()
      .then(setRescueTrancheSize)
      .catch(console.error)
  }, [doomCatRescue])

  return rescueTrancheSize
}

function useRescuePriceFinal(doomCatRescue, totalCats, genesisCats, rescuePriceInitial, rescuePriceIncrement, rescueTrancheSize) {
  const [rescuePriceFinal, setRescuePriceFinal] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue || !totalCats || !genesisCats || !rescuePriceInitial || !rescuePriceIncrement || !rescueTrancheSize || rescueTrancheSize.eq(0)) return

    setRescuePriceFinal(rescuePriceInitial.add(totalCats.sub(genesisCats).div(rescueTrancheSize).mul(rescuePriceIncrement)))
  }, [doomCatRescue, totalCats, genesisCats, rescuePriceInitial, rescuePriceIncrement, rescueTrancheSize])

  return rescuePriceFinal
}

function useRescueRateLimit(doomCatRescue) {
  const [rescueRateLimit, setRescueRateLimit] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue) return

    doomCatRescue.rescueRateLimit()
      .then(setRescueRateLimit)
      .catch(console.error)
  }, [doomCatRescue])

  return rescueRateLimit
}

function useRescueLastBlock(doomCatRescue, account) {
  const [rescueLastBlock, setRescueLastBlock] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!doomCatRescue || !account) return

    doomCatRescue.rescueLastBlock(account)
      .then(setRescueLastBlock)
      .catch(console.error)

    function updateRescueLastBlock(_, __, ___, options) {
      setRescueLastBlock(BigNumber.from(options.blockNumber))
    }

    const rescueLastBlockFilter = doomCatRescue.filters.Rescue(account, null, null)

    doomCatRescue.on(rescueLastBlockFilter, updateRescueLastBlock)

    return () => {
      doomCatRescue.removeListener(rescueLastBlockFilter, updateRescueLastBlock)
    }
  }, [doomCatRescue, account])

  return rescueLastBlock
}

export {
  useRescueStartTime,
  useTotalCats,
  useRemainingCats,
  useRescuePriceInitial,
  useRescuePrice,
  useRescuePriceIncrement,
  useRescueTrancheSize,
  useRescuePriceFinal,
  useRescueRateLimit,
  useRescueLastBlock,
}
