import { useEffect, useState } from 'react'
import Image from 'react-graceful-image'
import { BigNumber } from 'ethers'
import { Redirect, useParams, useLocation } from 'react-router-dom'
import Jumbo from "../components/Jumbo"
import AuctionInputs from '../components/AuctionInputs'
import AuctionDetails from "../components/AuctionDetails"
import { useWeb3 } from '../web3'
import { useData } from '../dataContext'
import { useAuctionCatInfo } from '../dataContext/cats'

const LoadingError = ({ children, catId }) => {
  return (
    <Jumbo>
      <div className="h-full text-center">
        <div className="text-xl">DoomCat #{catId.toString()}</div>
        <div className="flex flex-col justify-center h-full p-4">{children}</div>
      </div>
    </Jumbo>
  )
}
  
const AuctionCatDetails = () => {
  const { id } = useParams()
  const { doomCatContract, inAuctionNumber, catsPerAuction } = useData()
  const { account } = useWeb3()

  const location = useLocation()

  const [catId, setCatId] = useState(BigNumber.from(0))

  useEffect(() => {
    if (!isNaN(id)) {
      setCatId(BigNumber.from(id))
    } else {
      setCatId(null)
    }
  }, [id])

  const [tokenURI, setTokenURI] = useState()
  useEffect(() => {
    if (catId.eq(0)) return
    setTokenURI(`https://api.doom.cat/metadata/${catId}`)
  }, [catId])

  const [catInfo, error] = useAuctionCatInfo(doomCatContract, catId, tokenURI, account, location?.state?.catInfo)

  const [isInAuction, setIsInAuction] = useState(false)
  useEffect(() => {
    if (inAuctionNumber.eq(0) || !catInfo || !catInfo.id) {
      setIsInAuction(false)
      return
    }

    setIsInAuction(
      catInfo.id > inAuctionNumber.toNumber() * catsPerAuction.toNumber() - catsPerAuction.toNumber() &&
      catInfo.id <= inAuctionNumber.toNumber() * catsPerAuction.toNumber()
    )
  }, [catInfo, catsPerAuction, inAuctionNumber])

  if (catId === null) {
    return <Redirect to="/" />
  } else if (error) {
    return (
      <LoadingError catId={catId}>
        <div>Error: {error.message}</div>
      </LoadingError>
    )
  } else if (catInfo) {
    return (
      <Jumbo>
        <div className="mb-4 text-center text-xl">{catInfo.name}</div>
        <div className="text-xs mt-4">
          <div className="flex flex-col items-center mt-4">
            <AuctionInputs catInfo={catInfo} inAuction={isInAuction} />
            <AuctionDetails catInfo={catInfo} inAuction={isInAuction} /> 
          </div>
          <div className="max-w-md mx-auto">
            {catInfo.image && <Image
              src={catInfo.image}
              retry={{ count: 30, delay: 3 }}
              width="100%"
              alt={catInfo.name}
            />}
          </div>
        </div>
        <div className="flex flex-col items-center mt-4">
          <div>
            <div>ID: {catInfo.id}</div>
            {catInfo.attributes?.map((a, i) => {
              return (
                <div key={i}>{a.trait_type}: {a.value}</div>
              )
            })}
          </div>
        </div>
      </Jumbo>
    )
  } else {
    return (
      <LoadingError catId={catId}>
        <div>Loading...</div>
      </LoadingError>
    )
  }
}

export default AuctionCatDetails
