import { useState, useEffect } from 'react'
import { Contract } from 'ethers'
import DoomCatRescue from '../abis/DoomCatRescue.json'
import ERC20 from '../abis/ERC20.json'
import OpenSeaProxyABI from '../abis/OpenSeaProxyRegistry.json'

const useDoomCatContract = (provider, addresses) => {
  const [doomCatContract, setDoomCatContract] = useState(null)

  useEffect(() => {
    if (!provider || !addresses) return

    setDoomCatContract(new Contract(addresses.doomCatRescue, DoomCatRescue, provider))
  }, [addresses, provider])

  return doomCatContract
}

const useHateContract = (provider, doomCatRescue) => {
  const [hateContract, setHateContract] = useState(null)

  useEffect(() => {
    if (!provider || !doomCatRescue) return

    doomCatRescue.HATE()
      .then(tokenAddress => {
        setHateContract(new Contract(tokenAddress, ERC20, provider))
      }).catch(console.error);
  }, [doomCatRescue, provider])

  return hateContract
}

const useOpenSeaProxyRegistryContract = (provider, doomCatRescue) => {
  const [openSeaProxyRegistryContract, setOpenSeaProxyRegistryContract] = useState(null)

  useEffect(() => {
    if (!provider || !doomCatRescue) return

    doomCatRescue.proxyRegistryAddress()
      .then(proxyAddress => {
        setOpenSeaProxyRegistryContract(new Contract(proxyAddress, OpenSeaProxyABI, provider))
      })
      .catch(console.error)
  }, [doomCatRescue, provider])

  return openSeaProxyRegistryContract
}

export {
  useDoomCatContract,
  useHateContract,
  useOpenSeaProxyRegistryContract
}
