import { useState, useEffect, useRef, useCallback } from 'react'
import { BigNumber } from 'ethers'
import { Link } from 'react-router-dom'
import Jumbo from "../components/Jumbo"
import DoomCatCard from '../components/DoomCatCard'
import { useData } from '../dataContext'
import { useWeb3 } from '../web3'

const Ascended = () => {
  const { totalCats, remainingCats, genesisCats } = useData()
  const { account } = useWeb3()

  const [rescuedCount, setRescuedCount] = useState(BigNumber.from(0))
  useEffect(() => {
    if (!remainingCats || !totalCats || !genesisCats) return
    setRescuedCount(totalCats.sub(remainingCats).sub(genesisCats))
  }, [genesisCats, remainingCats, totalCats])

  const [catsPerPage] = useState(BigNumber.from(8))
  const [catsOnScreen, setCatsOnScreen] = useState(catsPerPage)
  const [catsLoaded, setCatsLoaded] = useState([])
  const bottomBoundaryRef = useRef(null)
  const scrollObserver = useCallback(node => {
    const intObs = new IntersectionObserver(entries => {
      entries.forEach(en => {
        if (en.intersectionRatio > 0 && catsOnScreen.eq(catsLoaded.length)) {
          setCatsOnScreen(catsOnScreen => catsOnScreen.add(catsPerPage))
          intObs.unobserve(node)
        }
      })
    })
    intObs.observe(node)
  }, [catsLoaded.length, catsOnScreen, catsPerPage])

  useEffect(() => {
    if (bottomBoundaryRef.current) {
      scrollObserver(bottomBoundaryRef.current)
    }
  }, [scrollObserver, bottomBoundaryRef])

  const loadedCallback = useCallback(catId => {
    setCatsLoaded(catsLoaded => {
      if (!catsLoaded.includes(catId.toString())) {
        return [...catsLoaded, catId.toString()]
      } else {
        return catsLoaded
      }
    })
  }, [])

  const [catIdsToShow, setCatIdsToShow] = useState([])
  useEffect(() => {
    if (!genesisCats) return

    setCatIdsToShow(
      [...new Array(catsOnScreen.toNumber()).keys()]
        .map(catIndex => BigNumber.from(catIndex).add(genesisCats).add(1).add(rescuedCount.sub(catsOnScreen)))
        .filter(id => genesisCats.lt(id))
    )
  }, [catsOnScreen, genesisCats, rescuedCount])

  const [catCards, setCatCards] = useState([])
  useEffect(() => {
    setCatCards(catCards => {
      const currentCardIds = catCards.map(card => card.key)
      const missingCardIds = catIdsToShow.filter(id => !currentCardIds.includes(id.toString()))
      const newCards = missingCardIds.map(id => <DoomCatCard tokenId={id} key={id.toNumber()} loadedCallback={loadedCallback} />)
      const allCards = [...catCards, ...newCards]
      const sortedCards = allCards.sort((a, b) => Number(b.key) - Number(a.key))
      return sortedCards
    })
  }, [catIdsToShow, loadedCallback])

  return (
    <Jumbo>
      <div className="mb-2 text-center text-xl italic">{rescuedCount.toString()} DoomCats have been Rescued</div>
      {account &&
        <div className="text-center text-purple">
          <Link to="/gallery">View My DoomCats</Link>
        </div>
      }
      <div className="grid grid-cols-2 md:grid-cols-4 gap-10 my-8">
        {catCards.length > 0 && catCards}
      </div>
      <div ref={bottomBoundaryRef} />
    </Jumbo>
  )
}

export default Ascended
