import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useData } from '../dataContext'
import { useWeb3 } from '../web3'

const OpenSeaMessageAccount = ({ accountCompare }) => {
  const { showOpenSeaMessage } = useData()
  const { account } = useWeb3()

  const [showOpenSeaMessageAccount, setShowOpenSeaMessageAccount] = useState(false)
  useEffect(() => {
    setShowOpenSeaMessageAccount(
      showOpenSeaMessage && 
      account.toLowerCase() === accountCompare.toLowerCase()
    )
  }, [account, accountCompare, showOpenSeaMessage])

  if (!showOpenSeaMessageAccount) {
    return null
  }

  return (
    <div className="not-italic text-xs mt-2">
      <div>Make sure you've <Link to="/open-sea-approval" className="text-purple">approved your proxy contract</Link> if you plan on selling on OpenSea.</div>
    </div>
  )
}

export default OpenSeaMessageAccount
